.main{
    display: none;
    text-align: center;
}
.NavigationItems ul
{   
    line-height: 5vw;
    padding:0px;
}
.NavigationItems li{
    
    align-items: center;
    position: relative;
    color: #a1a2a6;
    text-align: center;
    font-size: 20px;
    font-weight: 200;
    border: solid #f1f1f1 0.1px;
    vertical-align: middle;
} 
.NavigationItems li:hover {
    background-color: #0c5baf;
    border-radius: 5px;
    
}
.NavigationItems a:hover{
    text-decoration: none;
    cursor: pointer;
}
.NavigationItems span{
    width: 100%;
}

.NavigationItems a{
    text-decoration: none;
    color: #007bff;
}
.active li:nth-child(1){
    background-color: red;
}

a> li{
    background-color: #139BFF;
   
}
a > li >span{
    color: white;
}
@media (max-width: 600px) {
    .NavigationItems li{
        height: 100px;
        padding-top:40px;
        font-weight: bold;
    }
.extra
{
    display: none;
}
.main
{
    display: inline;
    text-align: center;
}
} 
   